.k-spreadsheet {
  width: 800px;
  height: 600px;
  position: relative;
  border-width: 1px;
  border-style: solid;
  cursor: default;
}
.k-spreadsheet .k-spreadsheet-pane {
  padding-right: 1px;
  position: absolute;
  border-width: 1px;
  border-bottom-width: 0;
  border-right-width: 0;
  border-style: solid;
  box-sizing: border-box;
  overflow: hidden;
}
.k-spreadsheet .k-spreadsheet-pane.k-top {
  border-top-width: 0;
}
.k-spreadsheet .k-spreadsheet-pane.k-left {
  border-left-width: 0;
}
.k-spreadsheet .k-spreadsheet-pane .k-spreadsheet-cell {
  position: absolute;
  white-space: pre;
  box-sizing: border-box;
  overflow: hidden;
  padding: 1px 3px;
  background-clip: padding-box;
}
.k-spreadsheet .k-spreadsheet-pane .k-spreadsheet-cell.k-spreadsheet-has-comment::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  left: auto;
  border-width: 3px;
  border-style: solid;
}
.k-spreadsheet .k-spreadsheet-pane .k-spreadsheet-cell-comment {
  position: absolute;
  white-space: pre-wrap;
  text-align: left;
  border-width: 1px;
  border-style: solid;
}
.k-spreadsheet .k-spreadsheet-vaxis,
.k-spreadsheet .k-spreadsheet-haxis {
  position: absolute;
  border: none;
}
.k-spreadsheet .k-spreadsheet-vaxis {
  top: 0;
  width: 1px;
}
.k-spreadsheet .k-spreadsheet-haxis {
  left: 0;
  height: 1px;
}
.k-spreadsheet .k-filter-range,
.k-spreadsheet .k-spreadsheet-selection {
  border-width: 1px;
  border-style: solid;
  position: absolute;
  box-sizing: border-box;
}
.k-spreadsheet .k-spreadsheet-active-cell {
  position: absolute;
  outline-color: transparent !important;
  box-sizing: border-box;
  z-index: 10;
}
.k-spreadsheet .k-spreadsheet-action-bar {
  border-width: 0 0 1px;
  border-style: solid;
  position: relative;
  font-size: 12px;
  font-family: Arial, Verdana, sans-serif;
  display: table;
  width: 100%;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-name-editor,
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-formula-bar {
  display: table-cell;
  vertical-align: middle;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-name-editor {
  position: relative;
  vertical-align: top;
  border-width: 0 1px 0 0;
  border-style: solid;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-name-editor .k-combobox {
  width: 9.5em;
  border-radius: 0;
  border-color: transparent;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-name-editor .k-combobox .k-dropdown-wrap {
  border-radius: 0;
  border-color: transparent;
  background-image: none;
  box-shadow: none;
  border-width: 0;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-name-editor .k-combobox .k-dropdown-wrap .k-input {
  border-radius: 0;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-name-editor .k-combobox .k-dropdown-wrap .k-select {
  border-radius: 0;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-formula-bar {
  width: 100%;
  padding-left: 31px;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-formula-bar::before {
  position: relative;
  display: inline-block;
  overflow: hidden;
  width: 1em;
  height: 1em;
  text-align: center;
  vertical-align: middle;
  background-image: none;
  font: 16px/1 'WebComponentsIcons';
  speak: none;
  font-variant: normal;
  text-transform: none;
  text-indent: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\E691";
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border-width: 0 1px 0 0;
  border-style: solid;
  width: 24px;
  margin-left: -24px;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-formula-bar .k-spreadsheet-formula-input.k-spreadsheet-array-formula:before {
  content: "{";
  font-weight: bold;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-formula-bar .k-spreadsheet-formula-input.k-spreadsheet-array-formula:after {
  content: "}";
  font-weight: bold;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-spreadsheet-formula-input {
  overflow: hidden;
  height: 2.004em;
  max-height: 2.004em;
  line-height: 1.65em;
  text-indent: 0.571em;
  border: 0;
  margin: 0;
}
.k-spreadsheet .k-spreadsheet-action-bar .k-state-border-down {
  padding-bottom: 0;
}
.k-spreadsheet .k-spreadsheet-formula-input {
  outline: none;
  border-width: 0;
  height: 26px;
  line-height: 20px;
  width: 100%;
  box-sizing: border-box;
  white-space: pre;
  padding: 3px 0.5em;
}
.k-spreadsheet .k-spreadsheet-cell-editor {
  position: absolute;
  display: none;
  padding: 0 3px;
  line-height: 19px;
  z-index: 2000;
  overflow: hidden;
  box-shadow: inset 0 0 0 1px #217346;
}
.k-spreadsheet > .k-spreadsheet-view {
  position: relative;
  font-size: 12px;
  font-family: Arial, Verdana, sans-serif;
}
.k-spreadsheet > .k-spreadsheet-view .k-state-disabled {
  pointer-events: auto;
}
.k-spreadsheet .k-tabstrip-wrapper {
  position: relative;
  line-height: 1.7em;
}
.k-spreadsheet .k-tabstrip-wrapper .k-tabstrip-items {
  padding: 0.3em 0 0;
}
.k-spreadsheet .k-spreadsheet-quick-access-toolbar {
  display: inline-block;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  padding: 0.3em 0.3em 0;
}
.k-spreadsheet .k-spreadsheet-quick-access-toolbar .k-button {
  padding: 0.3em;
  line-height: 1.4em;
  height: auto;
}
.k-spreadsheet .k-merged-cells-wrapper,
.k-spreadsheet .k-selection-wrapper,
.k-spreadsheet .k-filter-wrapper,
.k-spreadsheet .k-spreadsheet-row-header {
  position: relative;
}
.k-spreadsheet .k-spreadsheet-column-header {
  position: absolute;
}
.k-spreadsheet .k-filter-wrapper {
  pointer-events: none;
  z-index: 50;
}
.k-spreadsheet .k-spreadsheet-sheets-bar {
  border-width: 1px 0 0;
  font-size: 14px;
}
.k-spreadsheet .k-vertical-align-center {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.k-spreadsheet .k-vertical-align-bottom {
  position: relative;
  top: 100%;
  transform: translateY(-100%);
}
.k-spreadsheet .k-dirty {
  position: absolute;
  top: 0;
  left: 0;
  right: unset;
}
.k-spreadsheet .k-single-selection {
  cursor: crosshair;
}
.k-spreadsheet .k-single-selection::after {
  content: " ";
  height: 6px;
  width: 6px;
  position: absolute;
  display: block;
  bottom: 0;
  right: 0;
  border-radius: 50%;
  margin-bottom: -4px;
  margin-right: -4px;
  border-width: 1px;
  border-style: solid;
  z-index: 100;
}
.k-spreadsheet .k-spreadsheet-edit-container .k-single-selection::after {
  display: none;
}
.k-spreadsheet .k-auto-fill,
.k-spreadsheet .k-auto-fill-punch,
.k-spreadsheet .k-auto-fill-br-hint,
.k-spreadsheet .k-auto-fill-tr-hint,
.k-spreadsheet .k-auto-fill-bl-hint {
  position: absolute;
  box-sizing: border-box;
}
.k-spreadsheet .k-auto-fill {
  border-width: 1px;
  border-style: solid;
  cursor: crosshair;
}
.k-spreadsheet .k-auto-fill-wrapper {
  position: relative;
}
.k-spreadsheet .k-spreadsheet-vborder {
  position: absolute;
  border-left-style: solid;
  border-left-width: 1px;
}
.k-spreadsheet .k-spreadsheet-hborder {
  position: absolute;
  border-top-style: solid;
  border-top-width: 1px;
}
.k-spreadsheet-names-popup .k-item {
  position: relative;
}
.k-spreadsheet-names-popup .k-icon {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.7;
  cursor: pointer;
}
.k-spreadsheet-names-popup .k-icon:hover {
  opacity: 1;
}
.k-horizontal-resize {
  cursor: col-resize;
}
.k-vertical-resize {
  cursor: row-resize;
}
.k-spreadsheet-data,
.k-merged-cells-wrapper,
.k-selection-wrapper,
.k-spreadsheet-active-cell {
  cursor: cell;
}
.k-horizontal-resize .k-spreadsheet-data,
.k-horizontal-resize .k-merged-cells-wrapper,
.k-horizontal-resize .k-selection-wrapper,
.k-horizontal-resize .k-spreadsheet-active-cell {
  cursor: col-resize;
}
.k-vertical-resize .k-spreadsheet-data,
.k-vertical-resize .k-merged-cells-wrapper,
.k-vertical-resize .k-selection-wrapper,
.k-vertical-resize .k-spreadsheet-active-cell {
  cursor: row-resize;
}
.k-spreadsheet .k-colorpicker .k-tool-icon {
  overflow: visible;
}
.k-button.k-reset-color,
.k-button.k-custom-color {
  display: block;
  width: 100%;
  border-radius: 0;
  border: none;
  text-align: left;
  line-height: 2em;
}
.k-spreadsheet-colorpicker .k-colorpalette {
  border: 1px solid #dbdbdb;
  border-left: none;
  border-right: none;
  padding: 0.4em;
}
.k-spreadsheet-filter {
  position: absolute;
  cursor: pointer;
  pointer-events: all;
}
.k-spreadsheet-sample {
  float: right;
}
.k-spreadsheet-clipboard-paste,
.k-spreadsheet-clipboard {
  position: fixed;
  opacity: 0;
  top: 0;
  left: 0;
  overflow: hidden;
  padding: 0;
  margin: 0;
  border: 0;
  width: 1px;
  height: 1px;
  box-sizing: border-box;
}
textarea.k-spreadsheet-clipboard {
  position: fixed;
}
.k-spreadsheet-top-corner {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10000;
  border-width: 0 1px 1px 0;
  border-style: solid;
}
.k-spreadsheet-top-corner:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  overflow: hidden;
  position: absolute;
  bottom: 0;
  right: 0;
  border-width: 6px;
  border-style: solid;
}
.k-spreadsheet-scroller {
  width: 100%;
  height: 100%;
  /*commenting the overflow property as it has ben handled through code in kendo.spreadsheet.override.js*/
  -webkit-overflow-scrolling: touch;
  position: absolute;
  z-index: 1;
}
@media (hover: none), (pointer: coarse) {
  .k-spreadsheet-scroller {
    z-index: 3;
